import lodash from 'lodash'

import type { Component, ComponentOptions, Components, Config, ConfigObject, Configs, Source, StandardizedConfigs } from '../types'

const { assign, each, get, has, isObject, kebabCase, last, mapKeys, mapValues, merge, set, size, split, startCase, unset } = lodash

export type SourceConfigs = {
  getComponents?: () => Promise<Components>,
  getConfigs?: () => Promise<Configs>,
  getLabel?: () => string | Promise<string | undefined>,
  getName?: () => string | Promise<string | undefined>,
  source?: string
}

export type HelperFunctions = ReturnType<typeof getHelpers>

export type Definitions = {
  [key: string]: DefinitionComponent
}

/* eslint-disable no-unused-vars */
export type DefinitionComponent = {
  getConfigs?: (_: HelperFunctions) => Configs
  getSettings?: (_: HelperFunctions) => Configs
  getTranslations?: (_: HelperFunctions) => Configs
  getUI?: (_: HelperFunctions) => Configs,
  getOptions?: () => Component
}

/* eslint-enable no-unused-vars */

const getType = (config: Config) => {
  if (typeof config === `object`) {
    switch (typeof config.default) {
      case `boolean`:
        return `switch`
    }
  }

  return `input`
}

const standardizeConfig = (key: string, config: Config, skipDefaultCheck = false) => {

  if (isObject(config)) {
    if (!skipDefaultCheck && !has(config, `default`)) {
      throw new Error(`Config "${key}" is missing a "default" value.`)
    }
  } else {
    config = {
      default: config,
    }
  }

  return config as ConfigObject
}

const parseConfig = (key: string, config: Config, options: Partial<ConfigObject> = {}) => {

  config = standardizeConfig(key, config)

  delete config.key

  if (config.docLink) {
    const docLinkText = `Documentation on this feature can be found <a href="${config.docLink}">here</a>.`
    config.hint = config.hint ? docLinkText + `<br>${config.hint}` : docLinkText
    delete config.docLink
  }

  return assign({
    default: null, // This is placed here so that `default` appears as first property
    type: getType(config),
    label: startCase(last(split(key, `.`))),
    category: `setting`,
  } as ConfigObject, config, options) as ConfigObject
}

const parseConfigs = (
  configs: Configs,
  options: Partial<ConfigObject> = {},
  // eslint-disable-next-line no-unused-vars
  callback?: (key: string, config: Config) => void,
) => {

  configs = mapKeys(configs, (config: Config, key: string) => {
    return lodash.get(config, `key`, key)
  })

  return mapValues(configs, (config: Config, key: string) => {

    callback && callback(key, config)

    return parseConfig(key, config, options)
  }) as StandardizedConfigs
}

const parseUniqueConfigs = (set: Configs, configs: Configs, options: Partial<ConfigObject>) => {
  return parseConfigs(configs, options, (key) => {
    if (has(set, key)) {
      throw new Error(`Duplicate key: "${key}"`)
    }
  })
}

const parseConfigsIntoCollection = (set: Configs, configs: Configs, options: Partial<ComponentOptions>) => {
  merge(set, parseUniqueConfigs(set, configs, options))
}

const getHelpers = (components: Components, component: string) => {

  const groupTypes = {
    group: {
      path: `groups`,
    },
    tab: {
      path: `tabs`,
    },
  }

  const walkConfigs = (configs: Configs, callback = null) => {

    const walked: StandardizedConfigs = {}

    each(configs, (config, key) => {
      config = standardizeConfig(key, config)
      if (callback) {
        const {
          config: newConfig,
          key: newKey,
        } = callback(key, config)
        walked[newKey] = newConfig
      } else {
        walked[key] = config
      }
    })

    return walked
  }

  const setComponentGroup = (name: string, label: string, configs: Configs, type = `group`) => {

    let path: string = groupTypes[type].path
    let replacement: ComponentOptions = {}

    each(configs, config => {
      if (typeof config === `object` && config.group) {

        // TODO: This variable should handle cases where group value is deeply nested
        const groupExpanded = config.group

        each(groupTypes, groupType => {
          const group = get(components, [component, groupType.path, groupExpanded])
          if (group) {
            set(replacement, [groupType.path, groupExpanded], group)
            unset(components, [component, groupType.path, groupExpanded])

            const holder = get(components, [component, groupType.path], {})
            if (size(holder) === 0) {
              unset(components, [component, groupType.path])
            }
          }
        })
      }
    })

    // TODO: Need to think about this more
    // if (has(components, [component, path, name])) {
    //   throw new Error(`Duplicate ${type}: "${name}"`)
    // }

    set(components, [component, path, name], assign({
      label: label,
    }, replacement))
  }

  const addNested = (name: string, configs: Configs) => {
    return walkConfigs(configs, (key: string, config: Config) => {

      if (typeof config === `object`) {
        if (config.visibleIf) {
          config.visibleIf = `${name}.${config.visibleIf}`
        }

        config.longLabel = `${startCase(name)} ⇢ ${config.longLabel ? config.longLabel : (config.label ? config.label : startCase(key))}`
      }

      return {
        key: `${name}.${key}`,
        config,
      }
    })
  }

  const addGroup = (
    name: string,
    label: string,
    configs: Configs,
    nesting = true,
    type = `group`,
    /* eslint-disable-next-line no-unused-vars */
    callback?: (_: string, _2: Config) => void,
  ) => {
    setComponentGroup(name, label, configs, type)

    return walkConfigs(configs, (key: string, config: ConfigObject) => {
      config.group = config.group ? `${name}.${config.group}` : name

      if (callback) {
        callback(key, config)
      }

      return {
        key: nesting ? `${name}.${key}` : key,
        config,
      }
    })
  }

  const addFeature = (name: string, label: string, configs: Configs, enabled = false, nesting = true) => {

    if (has(configs, `enabled`)) {
      throw new Error(`Feature "${name}" should not contain a property named "enabled".`)
    }

    configs = assign({
      enabled: standardizeConfig(`enabled`, enabled),
    }, configs)

    configs = addGroup(name, label, configs, nesting, `group`, (key, config) => {
      if (key !== `enabled`) {
        typeof config === `object` && lodash.set(config, `visibleIf`, `${name}.enabled`)
      }
    })

    return configs as StandardizedConfigs
  }

  // eslint-disable-next-line no-unused-vars
  type AddFieldCallbackInnerFuncType = (key: string, config: Config) => void

  const addField = (
    key: string,
    // eslint-disable-next-line no-unused-vars
    callback?: (arg: { insert: AddFieldCallbackInnerFuncType, modify: AddFieldCallbackInnerFuncType }) => void,
  ) => {

    let fields: Configs = {}

    const visibleIf = `${key}.isVisible`

    const prettyKey = startCase(key)

    fields.isVisible = {
      default: true,
      label: `Visible`,
      longLabel: `${prettyKey} ⇢ Visible`,
      styleClasses: `col-md-2`,
    }

    fields.required = {
      default: false,
      label: `Required`,
      longLabel: `${prettyKey} ⇢ Required`,
      visibleIf: visibleIf,
      styleClasses: `col-md-2`,
    }

    fields.disabled = {
      default: false,
      label: `Disabled`,
      longLabel: `${prettyKey} ⇢ Disabled`,
      visibleIf: visibleIf,
      styleClasses: `col-md-2`,
    }

    fields.readonly = {
      default: false,
      label: `Readonly`,
      longLabel: `${prettyKey} ⇢ Readonly`,
      visibleIf: visibleIf,
      styleClasses: `col-md-2`,
    }

    fields.hasAutoFocus = {
      default: false,
      label: `Autofocus`,
      longLabel: `${prettyKey} ⇢ Autofocus`,
      visibleIf: visibleIf,
      styleClasses: `col-md-2`,
    }

    fields.pattern = {
      default: ``,
      label: `Pattern`,
      longLabel: `${prettyKey} ⇢ Pattern`,
      visibleIf: visibleIf,
      styleClasses: `col-md-12`,
    }

    if (callback) {

      const insert: AddFieldCallbackInnerFuncType = (key, config) => {

        if (has(fields, key)) {
          throw new Error(`Field ${key} already exists!`)
        }

        const parsedConfig = parseConfig(key, config, {
          visibleIf: visibleIf,
          styleClasses: `col-md-12`,
        })

        parsedConfig.longLabel = `${prettyKey} ⇢ ${parsedConfig.label}`

        fields[key] = parsedConfig
      }

      const modify: AddFieldCallbackInnerFuncType = (key, config) => {
        merge(fields[key], standardizeConfig(key, config, true))
      }

      callback && callback({
        insert,
        modify,
      })
    }

    return addGroup(key, startCase(key), fields, true)
  }

  const addTab = (label: string, configs: Configs) => {

    const name = kebabCase(label)

    return addGroup(name, label, configs, false, `tab`)
  }

  return {
    addNested,
    addFeature,
    addGroup,
    addTab,
    addField,
  }
}

const parseConfigsAndComponents = (definitions: Definitions) => {

  const configs: Configs = {}
  const components = parseComponents(definitions)

  each(definitions, (component, key) => {

    const {
      getConfigs,
      getSettings,
      getTranslations,
      getUI,
    } = component

    const options: Partial<ConfigObject> = {
      component: key,
    }

    const helpers = getHelpers(components, key)

    if (getConfigs) {
      parseConfigsIntoCollection(configs, getConfigs(helpers), options)
    } else {

      if (getSettings) {
        parseConfigsIntoCollection(configs, getSettings(helpers), assign(options, {
          category: `setting`,
        }))
      }

      if (getTranslations) {
        parseConfigsIntoCollection(configs, getTranslations(helpers), assign(options, {
          category: `translation`,
        }))
      }

      if (getUI) {
        parseConfigsIntoCollection(configs, getUI(helpers), assign(options, {
          category: `ui`,
        }))
      }
    }
  })

  return {
    configs,
    components,
  }
}

const parseComponent = (key: string, component?: ComponentOptions) => {
  return assign({
    label: startCase(last(split(key, `.`))),
    order: 0,
    toggleable: false,
    description: null,
    rootKey: null,
  }, component) as ComponentOptions
}

const parseComponents = (components: Components) => {
  return mapValues(components, (component, key: string) => {
    const { getOptions } = component

    return parseComponent(key, getOptions ? getOptions() : null)
  }) as Components
}

const parseComponentsFromConfigs = (configs: Configs, structure: Components = {}) => {

  // TODO: Place all these types in the "types" directory
  const components: Components = {}

  let hasUnsorted = false

  each(configs, config => {
    if (typeof config === `object` && has(config, `component`)) {
      if (!has(components, config.component)) {

        let options = {}
        if (has(structure, config.component)) {
          const { getOptions } = structure[config.component]
          options = getOptions ? getOptions() : structure[config.component]
        }

        components[config.component] = parseComponent(config.component, options)
      }
    } else {
      hasUnsorted = true
    }
  })

  if (hasUnsorted && size(components)) {
    components.unsorted = parseComponent(`unsorted`, {
      order: 999,
    })
  }

  return components
}

const parseSource = async ({ getComponents, getConfigs, getLabel, getName, source }: SourceConfigs) => {

  const name = getName ? await getName() : source
  const label = getLabel ? await getLabel() : startCase(name)

  const options: Source = {
    name,
    label,
  }

  const grabComponents = getComponents ? await getComponents() : null

  if (getConfigs) {
    const config = parseConfigs(await getConfigs())
    const components = parseComponentsFromConfigs(config, grabComponents)
    merge(options, {
      config,
      components,
    })
  } else {
    const { components, configs } = parseConfigsAndComponents(grabComponents)
    merge(options, {
      configs,
      components,
    })
  }

  return options
}

class SourceError extends Error {

  constructor(source, error) {
    super(`${error.message} ("${source}" source)`)
    this.name = `SourceError`
  }
}

export { parseSource, SourceError }
